'use client'
import { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { getWorkoutFromDay } from "@/app/actions";

const days = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"];

export default function WeekPlanner() {
    const userId = "557827b2-a6e2-49b9-9016-1d3bcf6a6422"; // Replace with dynamic userId if needed

    const [tasks, setTasks] = useState<{ [key: string]: string[] }>({});
    const [selectedDay, setSelectedDay] = useState("Monday");

    useEffect(() => {
        async function fetchTasks() {
            const workouts = await getWorkoutFromDay(userId, selectedDay);
            const tasksForDay = workouts.map(workout => workout.name); // Adjust this based on your DB schema and task structure
            setTasks((prevTasks) => ({
                ...prevTasks,
                [selectedDay]: tasksForDay,
            }));
        }

        fetchTasks();
    }, [selectedDay, userId]); // Dependency array ensures it fetches when selectedDay changes

    return (
        <div className="space-y-8 p-6">
            <h1 className="text-4xl font-bold">Week Planner</h1>
            <Tabs defaultValue="Monday" className="w-full">
                <TabsList className="grid grid-cols-7">
                    {days.map(day => (
                        <TabsTrigger
                            key={day}
                            value={day}
                            onClick={() => setSelectedDay(day)}
                        >
                            {day}
                        </TabsTrigger>
                    ))}
                </TabsList>
                {days.map(day => (
                    <TabsContent key={day} value={day}>
                        <Card>
                            <CardHeader>
                                <CardTitle>{day}</CardTitle>
                            </CardHeader>
                            <CardContent className="space-y-4">
                                <div className="flex gap-2">
                                    <Input
                                        placeholder="Add a task"
                                    />
                                    <Button>Add</Button>
                                </div>
                                <ul className="list-disc pl-5 space-y-1">
                                    {(tasks[day] || []).map((task, index) => (
                                        <li key={index}>{task}</li>
                                    ))}
                                </ul>
                            </CardContent>
                        </Card>
                    </TabsContent>
                ))}
            </Tabs>
        </div>
    );
}
